.login-container {
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 1rem;
  display: flex;
}

.login-card {
  background: var(--background-body, #fff);
  border-radius: 16px;
  width: 100%;
  max-width: 360px;
  padding: 2rem;
  box-shadow: 0 10px 40px #0000004d;
}

.login-header {
  text-align: center;
  margin-bottom: 2rem;
}

.login-icon {
  color: var(--links, #6db3f2);
  margin-bottom: 1rem;
}

.login-header h1 {
  color: var(--text-main, #333);
  margin: 0 0 .5rem;
  font-size: 1.75rem;
}

.login-subtitle {
  color: var(--text-muted, #666);
  margin: 0;
  font-size: .95rem;
}

.login-form {
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

.login-form .form-group {
  flex-direction: column;
  display: flex;
}

.login-form input {
  border: 2px solid var(--border, #ddd);
  text-align: center;
  letter-spacing: 2px;
  border-radius: 10px;
  padding: 1rem;
  font-size: 1rem;
  transition: border-color .2s, box-shadow .2s;
}

.login-form input:focus {
  border-color: var(--links, #6db3f2);
  outline: none;
  box-shadow: 0 0 0 3px #6db3f233;
}

.login-btn {
  background: var(--links, #6db3f2);
  color: #000;
  cursor: pointer;
  border: none;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  gap: .5rem;
  padding: 1rem;
  font-size: 1rem;
  font-weight: 600;
  transition: background .2s, transform .1s;
  display: flex;
}

.login-btn:hover {
  background: var(--links-hover, #9ecbf7);
}

.login-btn:active {
  transform: scale(.98);
}

.login-btn:disabled {
  opacity: .7;
  cursor: not-allowed;
}

.btn-spinner {
  align-items: center;
  display: flex;
}

.spinner {
  width: 20px;
  height: 20px;
  animation: 1s linear infinite spin;
}

@keyframes spin {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.login-error {
  color: #dc3545;
  text-align: center;
  background: #dc35451a;
  border-radius: 8px;
  margin: 0;
  padding: .75rem;
  font-size: .9rem;
}

.login-footer {
  text-align: center;
  margin-top: 2rem;
}

.login-footer a {
  color: #fff9;
  font-size: .85rem;
  text-decoration: none;
}

.login-footer a:hover {
  color: #ffffffe6;
}

.visually-hidden {
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

@media (prefers-color-scheme: dark) {
  .login-card {
    background: #1e1e1e;
  }

  .login-header h1 {
    color: #e0e0e0;
  }

  .login-form input {
    color: #e0e0e0;
    background: #2d2d2d;
    border-color: #444;
  }

  .login-btn {
    color: #000;
    background: #6db3f2;
  }
}

@media (width <= 480px) {
  .login-card {
    padding: 1.5rem;
  }

  .login-header h1 {
    font-size: 1.5rem;
  }

  .login-icon {
    width: 48px;
    height: 48px;
  }
}

